import React from "react";
import ReactDOM from "react-dom";

import "./index.css";

import pic1 from "./images/color-switch.jpg";
import pic2 from "./images/rummy.png";
import pic3 from "./images/motions.png";
import pic4 from "./images/vending.png";
import pic5 from "./images/terminal.jpg";
import pic6 from "./images/evildevil.png";

const Project = () => {
  return (
    <>
      <section class="projects" id="projects">
        <div class="max-width">
          <h2 class="title">My Projects</h2>
          <div class="carousel owl-carousel">
            <a href="https://github.com/SakshatMali/ColorSwitchGame" target='_blank'>
              <div class="card">
                <div class="box">
                  <img src={pic1} alt="" />
                  <div class="text">Color Switch Game</div>
                  <p>Built in JavaFX and Designed using FXML</p>
                </div>
              </div>
            </a>
            <a href="https://github.com/SakshatMali/Rummy-Game" target='_blank'>
              <div class="card">
                <div class="box">
                  <img src={pic2} alt="" />
                  <div class="text">Rummy Game</div>
                  <p>Built in Python by using PyGame Library</p>
                </div>
              </div>
            </a>
            <a href="https://github.com/SakshatMali/MotionStrikerGame" target='_blank'>
              <div class="card">
                <div class="box">
                  <img src={pic3} alt="" />
                  <div class="text">Motion Striker Game</div>
                  <p>
                    Built by using Arduino, Designed by using Processing.js and
                    Striker is controlled using Ultrasonic Sensor
                  </p>
                </div>
              </div>
            </a>
            <a href="https://github.com/SakshatMali/Source-Code-PIS-Major-Project" target='_blank'>
              <div class="card">
                <div class="box">
                  <img src={pic4} alt="" />
                  <div class="text">Gift Vending Machine</div>
                  <p>
                    Built by using Arduino, give gifts when plastic is given to
                    the machine.
                  </p>
                </div>
              </div>
            </a>
            <a href="https://github.com/SakshatMali/BasicLinuxTerminal" target='_blank'>
              <div class="card">
                <div class="box">
                  <img src={pic5} alt="" />
                  <div class="text">Linux Terminal</div>
                  <p>Built in C Language</p>
                </div>
              </div>
            </a>
            <a href="https://github.com/SakshatMali/evilDevil.github.io" target='_blank'>
              <div class="card">
                <div class="box">
                  <img src={pic6} alt="" />
                  <div class="text">Evil Devil Game</div>
                  <p>2D Game Built in JavaScript by using Phaser.js Library </p>
                </div>
              </div>
            </a>
          </div>
        </div>
      </section>
    </>
  );
};

export default Project;
