import React from "react";
import ReactDOM from "react-dom";
import myimg from "./images/me.jpg";
import Typical from "react-typical";
import { NavLink} from "react-router-dom";

import "./index.css";

const About = () => {
  return (
    <>
      <section className="about" id="about">
        <div className="max-width">
          <h2 className="title">About me</h2>
          <div className="about-content">
            <div className="column left">
              <img src={myimg} alt="" />
            </div>
            <div className="column right">
              <div className="text">
                I'm Sakshat and I'm a <span className="typing-2">
                <Typical
                  loop={Infinity}
                  wrapper="b"
                  steps={["Developer", 2000, "Designer", 2000, "Student", 2000]}
                ></Typical>
                </span>
              </div>
              <p>
                Hello, I am Sakshat Mali. Currently, I am a Computer Science Undergrad at IIIT Delhi.
                I love to Explore, Develop, Learn and Work with new and creative things. 
                I love Developing Games, Softawares, Mobile Applications and love to Design the front-end of Applications and Websites.
                I have bulit lots of cool projects and currently working of some more great projects.
                I like to do Competitive Programming and play Chess.
              </p>
              <NavLink to="/skill" className="menu-btn">
                Skills
              </NavLink>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default About;
